}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

a {
  cursor: pointer;
  color: inherit;
  outline: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
  display: block;
}

a:visited {
  outline: none;
  text-decoration: none;
}

ul, li {
  margin: 0;
