.loader {
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  background-color: #e5e5e5;
  justify-content: center;
  align-items: center;
  transition: all 2.5s, visibility 2.5s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loader-run {
  opacity: 0;
  visibility: hidden;
}

.loader:after {
  content: "";
  width: 95px;
  height: 95px;
  border: 15px solid #fff;
  border-top-color: #ff6b08;
  border-radius: 50%;
  animation: .8s infinite loading;
}

@keyframes loading {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(1turn);
  }
}

